import React, { Component } from 'react';
import TextareaAutosize from 'react-autosize-textarea';
import Switch from "react-switch";
import './NodeInput.css';


/// ---------------- THIS COMPONENT SHOULD PROBABLY BE SPLIT INTO MULTIPLE COMPONENTS ------------------------------



class NodeInput extends Component {
  constructor() {
    super();
    this.state = { checked: false};
  };

  callOnchangeFunction = (event) => {

  		if (this.props.nestedNodeAttribute === undefined) {
  			this.props.onChange(this.props.nodeAttribute, event.target.value);
  		} else {
  			this.props.onChange(this.props.nodeAttribute, event.target.value, this.props.nestedNodeAttribute);
  		}
  		
  }

  callOnchangeToggleFunction = (checked) => {
    // this.setState({checked})
    var node_type = (checked) ? "bot" : "user";
    this.props.onChange(this.props.nodeAttribute, node_type, this.props.nestedNodeAttribute);
  }

  // handleKeyPress = (event) => {
  //   if (event.key == "Enter") {
  //     this.callOnchangeFunction(event);
  //   }
  // }

  render() {

    // ----------- THIS ENTIRE SECTION IS GARBAGE -----------------
    var nodeExists = (this.props.node != null) && (this.props.node != undefined);
    var nodeText = "";
    var isBotResponse = false;
    if (nodeExists) {
      if (this.props.node.attributes == undefined) {
        console.log(this.props.node);
      }else {
        var nodeHasText = this.props.node.attributes.text != null;
        if (nodeHasText) {
          nodeText = this.props.node.attributes.text;
        } else {
          nodeText = "";
        }
        isBotResponse = this.props.node.attributes.node_type === "bot";
        if (isBotResponse) { isBotResponse = true;}
      } 
    }
    // ------------------------------------------------------------


    if (this.props.inputType === "text") {
      return (
        <div className="textInput">
          <label> {this.props.label} </label>
          <input 
          className="InputSection"
          onChange={this.callOnchangeFunction}
          value={this.props.author}>
            
          </input>
        </div> 
      );
    } else if (this.props.inputType === "textarea") {

      return (
        <div className="NodeInput">
          <label> {this.props.label} </label>
          <TextareaAutosize 
            value={nodeText}
            className="textarea"
            //onKeyPress={this.handleKeyPress}
            onChange={this.callOnchangeFunction}>
          </TextareaAutosize>
            
        </div> 
      );

    } else if (this.props.inputType === "toggle") {

      return (
        <div className="toggleSwitch">
          <label>
            <span>Bot Response</span>
            <Switch
              onChange={this.callOnchangeToggleFunction}
              checked={isBotResponse}
              //checked={this.state.checked}
              id="normal-switch"
            />
          </label>
            
        </div> 
      );

    }
  } 
}

export default NodeInput;


