import React, { Component } from 'react';
import ControlsContainer from "./ControlsContainer";
import './App.css';
import Tree from './Tree_Node';
import clone from 'clone'
import axios from 'axios';
import 'react-contexify/dist/ReactContexify.min.css';

import ReactTooltip from 'react-tooltip';

class App extends Component {
  constructor() {
    super();
    this.addedNodesCount = 0;
    this.state = {
      author: "example@example.com",
      data: null,
      nodeFocus: null,
      nodeSvgShape: {
        shape: 'circle',
        shapeProps: {
          r: 10,
        },
      },
      circleRadius: undefined,
      orientation: 'vertical',
      translateX: 200,
      translateY: 300,
      collapsible: true,
      initialDepth: 3,
      depthFactor: undefined,
      zoomable: true,
      zoom: 1,
      scaleExtent: { min: 0.1, max: 1.5 },
      separation: { siblings: 1.35, nonSiblings: 1.8 },
      nodeSize: { x: 140, y: 140 },
      transitionDuration: 500,
      buttonStyle: {
        visibility: 'hidden'
      },
      buttonName: "",
      buttonClick: null,
      buttonClickCollapse: null,
      styles: {
        nodes: {
          node: {
            circle: {
              fill: '#52e2c5',
            },
            attributes: {
              stroke: '#000',
            },
          },
          leafNode: {
            circle: {
              fill: 'transparent',
            },
            attributes: {
              stroke: '#000',
            },
          },
        },
      },
    };
    this.setTreeData = this.setTreeData.bind(this);
    this.setNodeFocus = this.setNodeFocus.bind(this);
  }

  promptForAuthor() {
    var author = prompt("Please enter your email address - this will be used to record your contributions.", this.state.author);
    this.setState({author: author});
    //console.log(this.state.author);
  }

  //function to update the statedata used for the tree
  setTreeData(data) {
    this.setState({ data });
  }

  //function to update the node in focus
  setNodeFocus = (nodeKey) => {
    this.setState({ nodeFocus: nodeKey });
  }

  // function used to find a node in the tree by its name using bfs
  getObjById = (name, data) => {
    var queue = [];
    queue.push(data[0]);

    while (queue.length > 0) {
      var x = queue.shift();
      if (x.name === name) {
        return x;
      }
      else if (x.children) {
        for (var i = 0; i < x.children.length; i++) {
          queue.push(x.children[i]);
        }
      }
    }
    return "node not found";
  }

  updateAuthor = (authorName) => {
    this.setState({
      author: authorName
    })
  } 

  // function to change the attributes of a node
  updateNode = (key, value, key2 = null) => {
    const nodeKey = this.state.nodeFocus.name;
    const data = clone(this.state.data);
    const node = this.getObjById(nodeKey, data);

    if (key2 === null) {
      node[key] = value;
    } else {
      if (!node[key]){
        node[key]={};
      }
      node[key][key2]= value;
    }
    this.setState({
      data
    });
    this.setState({nodeFocus: node});
  }

  //function to read tree data from mongodb
  componentDidMount() {
    axios.get('http://52.234.146.5:5000/full_tree')
    //axios.get('http://localhost:8000/full_tree')
      .then(response => {
        var parsedJSONTree = JSON.parse(response.data)
        this.setState({ data: [parsedJSONTree,] })
      });
    this.promptForAuthor();
  }

  // function for when the node is right clicked on, creates a child and changes focus
  buttonRightClick = (nodeKey) => {
    this.setState({ nodeFocus: nodeKey });
    this.showEditButtons();
    //this.setState({ nodeFocus: nodeKey }, ()=> this.addChildNode());
  }

  showEditButtons = () => {
    this.setState({
      buttonStyle: {
        visibility: 'visible'
      },
    });
  }

  //creates a new child at the node and changes the focus to that node
  addChildNode = () => {
    const data = clone(this.state.data);
    const obj = this.getObjById(this.state.nodeFocus.name, data);
    var target = obj.children ? obj.children : obj._child;
    this.addedNodesCount++;
    if (target === undefined) {
      obj.children = [];
      target = obj.children;
    }
    target.push({ 
      name: `Inserted Node ${this.addedNodesCount}`, 
      id: `inserted-node-${this.addedNodesCount}` 
    });
    this.expandAfterAddNode(obj, data);
    this.setState({nodeFocus: target[target.length - 1]}, ()=>this.populateNewNode());
  }

  // if a child is created
  expandAfterAddNode = (obj, data) => {
    if (obj._child) {
      obj.children = obj._child;
      obj._child = null;
      this.setTreeData(data);
    }
    this.setTreeData(data);
  }

  

  populateNewNode = () => {
    var node = clone(this.state.nodeFocus);
    node.attributes = {
      author:"",
      text:"",
      node_type: "bot"
    };
    node.name = this.state.nodeFocus.name;
    node.type = "";
    this.setNodeFocus(node);
    this.setState({
      buttonStyle: {
        visibility: 'visible'
      },
    });
  }

  render() {
    if (this.state.data === null) {
      return ("loading...");
    } else {
      return (
        <div className="App">
          <div className="demo-container">
            <div className="column-left">
              <ControlsContainer
                  author={this.state.author}
                  nodeFocus={this.state.nodeFocus}
                  buttonStyle={this.state.buttonStyle}
                  updateNode={this.updateNode}
                  updateAuthor={this.updateAuthor}
                  addChildNode={this.addChildNode}
                />
            </div>

                <div className="column-right">
                  <div ref={tc => (this.treeContainer = tc)} className="tree-container">
                    {/* <ContextMenuProvider id="menu_id">
                  <h1>hi</h1>
                </ContextMenuProvider> */}
                    {/* <MyAwesomeMenu /> */}

                    <Tree
                      data={this.state.data}
                      nodeSvgShape={this.state.nodeSvgShape}
                      circleRadius={this.state.circleRadius}
                      onClick={this.state.onClick}
                      onContextMenu={this.buttonRightClick}
                      orientation={this.state.orientation}
                      translate={{ x: this.state.translateX, y: this.state.translateY }}
                      pathFunc={this.state.pathFunc}
                      collapsible={this.state.collapsible}
                      initialDepth={this.state.initialDepth}
                      zoomable={this.state.zoomable}
                      zoom={this.state.zoom}
                      scaleExtent={this.state.scaleExtent}
                      nodeSize={this.state.nodeSize}
                      separation={this.state.separation}
                      transitionDuration={this.state.transitionDuration}
                      depthFactor={this.state.depthFactor}
                      textLayout={this.state.textLayout}
                      styles={this.state.styles}
                    />
                  </div>
                </div>
              </div>
            </div>
            );
      
          }
        }
      }
export default App;


