export const myContextMenu = (node, event) => {
    select('#test')
        .style('left', (event.pageX - 2) + 'px')
        .style('top', (event.pageY - 2) + 'px')
        .style('display', 'block');
    event.preventDefault();
    event.stopPropagation();
};



