import React, { Component } from 'react';
import NodeInput from "./NodeInput";
import './ControlsContainer.css';


class ControlsContainer extends Component {
  render() {
    var currentFocus = ((this.props.nodeFocus === null) ? "None" : this.props.nodeFocus.name);

    return (

      <div className="controls-container">
        <div className="prop-container" >         
          <h3>Editing Node: "{currentFocus}"</h3>
          <hr/>
          <a>Author: {this.props.author}</a>
          <p>If this email address is incorrect, please refresh the page and enter the correct address.</p>    
        </div>

        <div className="prop-container" style={this.props.buttonStyle}>

            <NodeInput
              inputType="toggle"
              label="Node Type"
              nodeAttribute="attributes"
              nestedNodeAttribute="node_type"
              node={this.props.nodeFocus}
              onChange={this.props.updateNode}/>

            <NodeInput
              inputType="textarea"
              label="Node Text"
              nodeAttribute="attributes"
              nestedNodeAttribute="text"
              node={this.props.nodeFocus}
              onChange={this.props.updateNode}/>

            <button className="controls-button" onClick={this.props.addChildNode}>
              Add Child Node
            </button>
      </div>

      <div className="prop-container">
        <h2>Instructions</h2>
        <hr/>
        <a>
          <b>Left click:</b> expand/collapse node children. <br/>
          <b>Right click:</b> edit node. <br/>
          <b>Bot response:</b> determines whether node contains a bot response, 
          or an option to be presented to the user. <br/>
          <b>Node text:</b> What the bot will say, or the option to be presented to the user. <br/>
        </a>
      </div>

    </div>
    );
  }
}

export default ControlsContainer;


